
.menu-bar {
  width: 100%;
  height: auto;
  background: #F5F5F5;
  box-shadow: 2px 0px 0 #BDBDBD;
  position: fixed;
}
.menu-bar main {
    height: 50px;
    margin: 1em 2em 1em 2em;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}
.menu-bar main p i {
      font-size: 1em;
      margin: 0 0 0 0.5em;
}
.menu-bar main i {
      font-size: 2em;
      margin: auto 0 auto 0;
}



